.post {
  max-width: 620px;
  margin-bottom: 48px;
  padding: 16px 32px 48px;

  text-align: left;
  box-sizing: border-box;
  background-color: rgba(231,215,226, 1);
  border: 2px solid rgba(231,137,201, 1);
  box-shadow: 0 8px 12px rgba(231,137,201, 0.4);
  cursor: pointer;
}

.post__header {
  display: grid;
  grid-template-columns: auto 1fr;
  align-items: baseline;
}
